// Reference:
// https://commons.apache.org/proper/commons-validator/
// https://www.owasp.org/index.php/OWASP_Validation_Regex_Repository

/**
 * @module lib-bb-email
 * @description Email validation library
 */

import re from './constants';
import topLevelDomains from './top-level-domains';

/**
 * Validates email structure defined in OWASP Regex Repository
 *
 * @name isValidEmail
 * @type {function}
 * @param {string} email Contact email address
 * @returns {boolean}
 */
export default (email) => {
  if (typeof email !== 'string') {
    return false;
  }

  if (!re.test(String(email).toLowerCase())) {
    return false;
  }

  try {
    return Boolean(topLevelDomains(email));
  } catch (error) {
    return false;
  }
};



